<template>
    <section>
        <h1>{{ entry.title }}</h1>
        <div style="margin-bottom:30px;">
            <p class="cInfo">Author : {{ entry.userId }} , Date : {{ this.dat }}</p>
            
            <img v-bind:src="entry.imgUrl" v-if="entry.imgUrl != ''" width="100%" style="margin-top:10px">

            <h2>Info</h2>
            <p data-v-event>このページはゲスト用です。@TJC会員登録済の方は会員用ページへお進みください。</p>
            <router-link v-bind:to="'/entries/detail/' + entry.no"><div class="buttonBase">会員用ページ</div></router-link>
            
            <h2>Content</h2>
            <div v-html="$sanitize(entry.body)" class="cBody"></div>
        </div>

    </section>
</template>

<script>
export default {
    name : "GuestEntry",
    data(){
        return {
        }
    },

    computed: {
        entry() {
            return this.$store.getters.entry;
        },
        dat(){
            let date = new Date(this.entry.dat)
            let y = date.getFullYear()
            let m = ("00" + (date.getMonth()+1)).slice(-2);
            let d = ("00" + date.getDate()).slice(-2)
            let datStr = y + "/" + m + "/" + d
            
            return datStr
        }
    },

    async mounted()
    {
        window.scrollTo(0,0);
        // hide toggre
        this.$store.dispatch('setToggreState', false)

        await this.$store.dispatch('initGuestEntry', this.$route.params.entryNo)
    },
    watch :{
        async '$route' (to, from){
            // hide toggre
            this.$store.dispatch('setToggreState', false)
        
            await this.$store.dispatch('initGuestEntry', this.$route.params.entryNo)
        }
    },

    methods:{
        async delEntry()
        {
            await this.$store.dispatch('delEntry', this.entry.no)
        },
    }


}
</script>

<style scoped>
    h1
    {
        font-size: 150%;
        margin: 30px 0px 10px 0px;
    }
    h2
    {
        font-size: 110%;
        font-weight: normal;
        margin: 20px 0px 10px 0px;
    }
    .cInfo{
        font-size:90%;
        margin: 5px 0px;
    }

    .cBody{
        margin-top:15px;
        line-height: 180%;
    }

</style>





