<template>
    <div>
        <article>
            <p>招待コードを入力してください。</p>
            <p>招待コードをお持ちでない方は、OB会WEBページから即時発行可能です。</p>
            <input type="text" name="invitationCd" placeholder="Invitation Code" v-model="invCode" class="topInput">

            <div class="buttonBase" v-on:click="toInput">次へ</div>
        </article>
        <article style="margin-top:50px;">
            <p>また下記 招待申請フォームからも招待コード発行申請を受け付けています。<br>（招待コード発行まで、少々お時間をいただく場合があります）</p>
            <input type="text" name="mail" placeholder="Email 必須" v-model="req.mail" class="topInput">
            <input type="text" name="name" placeholder="お名前 必須" v-model="req.name" class="reqInput">
            <input type="text" name="comment" placeholder="コメント等" v-model="req.comment" class="reqInput">
            <div class="buttonBase" v-on:click="toReq">招待コード発行申請</div>

        </article>
    </div>
</template>

<script>
export default {
    name : "InputInvitation",

    data(){
        return{
            invCode : "",
            req : {
                mail : "",
                name : "",
                comment : ""
            }
        }
    },

    created()
    {
        // URLパラメータ取得
        let query = this.$route.query
        console.log(JSON.stringify(query))
        if(query != null && query.invCode != null)
        {
            this.invCode = query.invCode
        }
    },

    methods:{
        toInput(){
            this.$store.dispatch('setInvCode', this.invCode)
            this.$router.push({name:'reg-input'})
        },

        toReq()
        {
            this.$store.dispatch('reqInvCd', this.req)
        }
    }
}
</script>

<style scoped>
    .topInput{
        margin: 30px 0px 5px 0px;
    }
    .reqInput{
        margin:5px 0px;
    }
</style>


