<template>
    <section>
        <h1 data-v-members>Member List</h1>

        <div  v-for="(member, index) in members" :key="index" data-v-members style="margin: 0px 0px 50px 0px;">
            <div style="float:left; margin-right:10px;">
                <router-link v-bind:to="'/profile/' + member.userNo" style="border:none;">
                    <img src="@/assets/prof.png" style="height:70px;" v-if="member.profIcon==null">
                    <img v-bind:src="member.profIcon" style="height:70px;" v-if="member.profIcon!=null">
                </router-link>
            </div>
            <div>
                <h2 data-v-members><router-link v-bind:to="'/profile/' + member.userNo">{{ member.loginId }}</router-link></h2>
                <h3 data-v-members>{{member.comment}}<br></h3>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name : "Members",
    computed: {
        members() {
        return this.$store.getters.members;
        }
    },

    mounted()
    {
        this.$store.dispatch('initMemberlist')
    },
    
    data: function () {
        return {
        }
    }
}   
</script>

<style scoped>
    h1[data-v-members] {
        font-size: 150%;
    }

    h2[data-v-members] {
        font-size: 120%;
        font-weight: normal;
        margin: 15px 0px 3px 10px;
    }

    h3[data-v-members] {
        font-size: 80%;
        font-weight: normal;
        margin: 5px 0px 0px 10px;
    }
</style>


